body {
  background-color: #111;
  margin: 0;
  font-family: Roboto, sans serif;
}

* {
  box-sizing: border-box;
}

h1, h3 {
  font-weight: 400;
}

.weather-app {
  min-height: 100vh;
  color: #fff;
  opacity: 1;
  background-image: url("cloudy.4bbe651f.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all .5s;
  position: relative;
}

.weather-app:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba (0, 0, 0, 0, .3);
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.container {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 2em 3em 4em;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.container > div {
  justify-content: center;
  align-items: center;
  display: flex;
}

.city-time, .temp, .weather {
  margin: 0 1em;
}

.city-time h1 {
  margin: 0 0 .2em;
  font-size: 3em;
}

.temp {
  margin: 0;
  font-size: 7em;
}

.weather img {
  margin: .5em 0;
  display: block;
}

.panel {
  width: 40%;
  height: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  z-index: 1;
  background-color: rgba(110, 110, 110, .25);
  border: 1px solid rgba(225, 225, 225, .18);
  padding: 3em 2em;
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: scroll;
  box-shadow: 0 8px 32px rgba(0, 0, 0, .3);
}

.panel form {
  margin-bottom: 3em;
}

.submit {
  cursor: pointer;
  background-color: #e06929;
  border: none;
  outline: none;
  margin: 0;
  padding: 1.5em;
  font-size: 1.2em;
  transition: all .4s;
  position: absolute;
  top: 0;
  right: 0;
}

.submit:hover {
  color: #000;
  background-color: #fff !important;
}

.search {
  width: 80%;
  color: #fff;
  background: none;
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 0 1em .5em 0;
  font-size: 1.1em;
}

.search:focus {
  outline: none;
}

.search::-ms-input-placeholder {
  color: #ccc;
}

.search::placeholder {
  color: #ccc;
}

.panel ul {
  border-bottom: 1px solid #ccc;
  margin: 2em 0;
  padding: 0 0 1em;
}

.panel ul li {
  color: #ccc;
  margin: 2.5em 0;
}

.panel ul h4 {
  margin: 3em 0;
}

.city {
  cursor: pointer;
  display: block;
}

.city:hover {
  color: #fff;
}

.details li {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 800px) {
  .panel, .container {
    width: 100%;
    top: initial;
    position: relative;
  }
}

@media screen and (max-width: 500px) {
  html {
    font-size: 12px;
  }
}

@media screen and (max-height: 300px) {
  .weather-app {
    min-height: 40em;
  }
}

